function addMarkerOnePoint(lng, lat, index) {
    var marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),
        zIndex: index
    });

    // 自定义点标记内容
    var html = "<div class=\"my-mkr\"><img src=\"//webapi.amap.com/theme/v1.3/markers/n/mark_r.png\" alt=\"" + index + "\" class=\"mkr-img\"><span class=\"mkr-txt\">" + index + "</span></div>";

    marker.setContent(html); //更新点标记内容

    return marker;
}

